.profile {
    grid-area: allPosts;
    display: flex;
    justify-content: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: rgb(243, 243, 243);
}

.profile-top {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 20px;
    align-items: center;
}

img.profile-pic, img.profile-pic:hover {
    filter: invert();
    width: 150px;
}

.followBtn {
    margin-top: 10px;
    height: 30px;
    width: 80px;
    border: none;
    border-radius: 30px;
    background-color: rgb(90, 71, 202);
    border: solid 2px rgb(90, 71, 202);
}

.followBtn:hover, .followBtn:active{
    color: rgb(243, 243, 243);
    background-color: #003049;
    border: solid 2px rgb(90, 71, 202);
    transition: 0.2s;
}

.profile-username {
    margin: 0;
}

.followers-panel {
    display: flex;
}

.following-followers {
    margin-right: 30px;
    font-size: 0.9em;
}

.following-follower-title {
    cursor: pointer;
}

.users-modal {
    background-color: rgba(114, 114, 114, 0.24);
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.allUsers {
    border: solid 2px #00345294;
    border-radius: 10px;
    background-color: #003049;
    width: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    padding: 5px;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.479);
}

.users-profile{
    text-align: center;
    margin: 5px;
    width: 100%;
}
